@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
*{box-sizing:border-box}html{font-size:3vh;font-family:"Lato",sans-serif}body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.header{position:absolute;top:0%;left:0%;margin:5px;border-bottom:6px solid #9c88ff}
.form{position:absolute;top:100px;left:50%;transform:translate(-50%, 0);display:flex;flex-direction:column;justify-content:center;justify-items:center;width:50vw;border:1px solid #000;border-radius:3px;padding:5px}.form .form-control{padding:1%;margin:10px}.form .form-control .input-label{padding:10px}.form .form-control .pizza-options{display:flex}.form .form-control .pizza-options div{margin:2px}.form .type{height:20vh}.form select,.form input{font-size:inherit}.form .submit{align-self:center;cursor:pointer;background-color:#9c88ff;box-shadow:0 1px 3px rgba(0,0,0,.12);color:#fff;border:0;display:block;font-size:1.34rem;margin:10px 0 30px;padding:10px;width:25%;border-radius:10px}.form .submit:hover{background-color:#7465be}
.footer{position:absolute;bottom:0;left:50%;transform:translate(-50%, 0);font-size:.4em}
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

